/**
 * 获取资源
 */
import axios from '@/common/axios.js'
import {
	getVideoList
} from '@/common/config.js'

const url = 'http://cj.lziapi.com/api.php/provide/vod/'

export const getTuijianVideo = (option) => {
	let type_id = option.type_id;
	return axios({
		url: url + `?t=${type_id}&ac=videolist&pg=${option.pagenum}`
	}).then((res) => {
		const arr = []
		for (let item of res.list) {
			item.vod_play_url_video =  getVideoList(item,'量子资源')
			arr.push(item)
		}
		return arr
	})
}

