import axios from '@/common/axios.js'
import KuwoDES from './KuwoDES'
import kuwoDES from "./KuwoDES";

export const videoList = (params) => {
  return axios({
    url: `https://www.kuwo.cn/search/searchMusicBykeyWord?vipver=1&client=kt&ft=music&cluster=0&strategy=2012&encoding=utf8&rformat=json&mobi=1&issubtitle=1&show_copyright_off=1&pn=${params.num}&rn=20&all=${params.key}`,
  })
}
export const videoItem = (params) => {
  const q = KuwoDES.encryptQuery(`user=0&android_id=0&prod=kwplayerhd_ar_4.3.0.8&corp=kuwo&vipver=4.3.0.8&source=kwplayerhd_ar_4.3.0.8_tianbao_T1A_qirui.apk&notrace=0&type=convert_url2&br=320&format=mp3&sig=0&rid=${params.rid}&priority=bitrate&loginUid=0&network=WIFI&loginSid=0&mode=down`)
  return axios({
    url: `https://nmobi.kuwo.cn//mobi.s?f=kuwo&q=${q}`,
  }).then(res=>{
    return kuwoDES.parseParamsToJson(res)
  })
}